// @mui
import { Container, Typography, Card, Box, Stack, Alert, Chip } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { CodeBlock, EndpointCard, ParameterTable } from '../../sections/docs';
import { HOST_API } from '../../config';

// ----------------------------------------------------------------------

export default function DocsVerifyIUC() {
  const { themeStretch } = useSettings();
  const { setting, user } = useAuth();

  const requestCode = `$ch = curl_init();
curl_setopt($ch, CURLOPT_URL, "${HOST_API}/api/validataiuc");
curl_setopt($ch, CURLOPT_POST, 1);
curl_setopt($ch, CURLOPT_POSTFIELDS, http_build_query([
    'cablename' => 1,
    'smart_card_number' => '1234567890'
]));
curl_setopt($ch, CURLOPT_RETURNTRANSFER, 1);
curl_setopt(
    $ch, CURLOPT_HTTPHEADER, [
        "Authorization: Bearer {$user?.apikey}",
        "Content-Type: application/x-www-form-urlencoded"
    ]
);
$response = curl_exec($ch);
curl_close($ch);`;

  const successResponse = `{
    "status": "success",
    "message": "IUC validation successful",
    "data": {
        "customer_name": "Habukhan Developer",
        "smart_card_number": "1234567890",
        "cable_provider": "DSTV",
        "current_bouquet": "Compact Plus",
        "renewal_amount": "9900.00",
        "due_date": "2024-02-15"
    }
}`;

  const parameters = [
    {
      name: 'cablename',
      type: 'integer',
      required: true,
      description: 'Cable provider ID (see cable IDs reference)',
    },
    {
      name: 'smart_card_number',
      type: 'string',
      required: true,
      description: 'Customer IUC/Smartcard number',
    },
  ];

  return (
    <Page title="Verify IUC Number">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Verify IUC Number"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Documentation', href: '/documentation/home' },
            { name: 'Verify IUC' },
          ]}
        />

        {/* Overview */}
        <Card sx={{ p: 4, mb: 3 }}>
          <Stack direction="row" alignItems="center" spacing={2} sx={{ mb: 2 }}>
            <Typography variant="h4">Verify IUC Number</Typography>
            <Chip label="POST" color="success" size="small" />
          </Stack>
          <Typography variant="body1" color="text.secondary">
            Verify cable TV IUC/Smartcard number before making payment. Returns customer name and current bouquet
            details.
          </Typography>
        </Card>

        {/* Endpoint */}
        <EndpointCard
          method="POST"
          endpoint={`${HOST_API}/api/validataiuc`}
          description="Verify IUC/Smartcard number validity"
        >
          <ParameterTable parameters={parameters} title="Request Parameters" />

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Code Example (PHP)
            </Typography>
            <CodeBlock code={requestCode} language="php" title="PHP" />
          </Box>

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Success Response
            </Typography>
            <CodeBlock code={successResponse} language="json" title="JSON Response" />
          </Box>
        </EndpointCard>

        {/* Important Notes */}
        <Card sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Important Notes
          </Typography>
          <Stack spacing={2}>
            <Alert severity="warning">
              <Typography variant="body2">
                <strong>Always Verify:</strong> Always verify IUC before making payment to avoid failed
                transactions
              </Typography>
            </Alert>
            <Alert severity="info">
              <Typography variant="body2">
                <strong>Free Verification:</strong> IUC verification is free and does not deduct from your
                balance
              </Typography>
            </Alert>
          </Stack>
        </Card>

      </Container>
    </Page>
  );
}
